import {useEffect, useState} from "react";
import React from 'react';
import Loading from "./Loading";

function FaqList() {
    const [faq, setFaq] = useState([]);
    const [loading, setLoading] = useState(true);
    // const [error, setError] = useState(null);
    useEffect(() => {
        // fetch("https://api.myjson.com/bins/1hjv7i")
        //     .then((response) => response.json())
        //     .then((data) => {
        //         setFaq(data);
        //         setLoading(false);
        //     }).catch(err => {
        //         setError(err);
        //         setLoading(false);
        //     });
        setFaq([
            {
                "id": 0,
                "question": "Como me registro?",
                "answer": "Para se registrar, clique no link abaixo e preencha os dados corretamente.",
                'link': (<a href="/cadastrar">Cadastrar</a>)
            },
            {
                "id": 1,
                "question": "Como fao para entrar?",
                "answer": "Para entrar, clique no link abaixo e preencha os dados corretamente.",
                'link': (<a href="/entrar">Entrar</a>)
            },
            {
                "id": 2,
                "question": "Recuperar senha perdida",
                "answer": "Para alterar sua senha, clique no link abaixo e preencha os dados corretamente.",
                'link': (<a href="/recuperar-senha">Recuperar Senha</a>)
            },
            {
                "id": 3,
                "question": "Error 204.",
                "answer": "No  exatamente um erro: o servidor atendeu com sucesso a solicitao e que no h contedo adicional para enviar no corpo de carga de resposta.",
                'link': ''
            },
            {
                "id": 4,
                "question": "Error 400.",
                "answer": "O servidor no pode ou no processar a solicitao devido a algo que  percebido como um erro desconhecido, contate o suporte.",
                'link': ''
            },
            {
                "id": 5,
                "question": "Error 401.",
                "answer": "A solicitao no foi aplicada porque no possui credenciais de autenticao vlidas para o recurso de destino.",
                'link': ''
            },
            {
                "id": 6,
                "question": "Error 403.",
                "answer": "O servidor entendeu o pedido, mas se recusa a autoriz-lo. Voc no pode realizar essa ao no momento.",
                'link': ''
            },
            {
                "id": 7,
                "question": "Error 404.",
                "answer": "O servidor de origem no encontrou uma representao atual para o recurso de destino ou no est disposto a revelar que existe.",
                'link': ''
            },
            {
                "id": 8,
                "question": "Error 405.",
                "answer": "O mtodo recebido na linha de solicitao  conhecido pelo servidor de origem, mas no suportado pelo recurso de destino.",
                'link': ''
            },
            {
                "id": 9,
                "question": "Error 412.",
                "answer": "Uma ou mais condies dadas nos campos de cabealho de solicitao avaliados como falsas quando testados no servidor.",
                'link': ''
            },
            {
                "id": 10,
                "question": "Error 500.",
                "answer": "Houve uma falha de comunicao em nossos servidores, primeiro verifique se sua conexo  estvel e se persistir, entre em contato conosco.",
                'link': ''
            },
            {
                "id": 11,
                "question": "Error 503.",
                "answer": "No momento, o servidor no consegue lidar com a solicitao devido a uma sobrecarga temporria ou manuteno programada, que provavelmente ser aliviada aps algum atraso.",
                'link': ''
            },
        ]);
        setTimeout(() => {
            setLoading(false);
        }, 1000);
    }, []);

    if (loading) {
        return <Loading />;
    }

    return (
        <div className="row">
            {faq.map(faq => (
                <div className="col-12 col-xl-6" key={faq.id}>
                    <div className="accordion-item">
                        <h2 className="accordion-header" id={'headingOne'+faq.id}>
                        <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target={'#collapseOne'+faq.id} aria-expanded="false" aria-controls={'collapseOne'+faq.id}>
                        {faq.question}
                        </button>
                        </h2>
                        <div id={'collapseOne'+faq.id} className="accordion-collapse collapse" aria-labelledby={'headingOne'+faq.id} data-bs-parent="#accordionExample">
                        <div className="accordion-body">
                            {faq.answer} {faq.link}
                        </div>
                        </div>
                    </div>
                </div>
            ))}
        </div>
    );
}
export default FaqList;